import React from 'react'
import { graphql } from 'gatsby'
import { GatsbyImage } from "gatsby-plugin-image";
import Layout from "/src/layouts/main/layout/layout.js";
import Markdown from "react-markdown";
import Seo from "/src/components/seo"

import "/src/assets/scss/templates/article.scss"

const ArticleTemplate = ({ data }) => {
  /* Usar imagen en el top con parallax */
  const articleTitle = data.strapiArticle.title,
  articleContent = data.strapiArticle.content,
  articleImage = data.strapiArticle.image.localFile.childImageSharp.gatsbyImageData;
  

  return (
    <Layout>
      <div className="article-content">
        <Seo />
        <h1>{articleTitle}</h1>
        {/* <p>by <Link to={`/authors/User_${data.strapiArticle.author ? data.strapiArticle.author.id : ''}`}>{data.strapiArticle.author ? data.strapiArticle.author.username : 'An√≥nimo'}</Link></p> */}
        <GatsbyImage image={articleImage} />
        <Markdown 
          escapeHtml={false} 
          transformImageUri={uri => uri.startsWith('http') ? uri : `http://jlpa-blog_back:1337${uri}`}
        >
          {articleContent}
        </Markdown>
      </div>
    </Layout>
  )
  
}

export default ArticleTemplate

export const query = graphql`query ArticleTemplate($id: String!) {
  strapiArticle(id: {eq: $id}) {
    title
    content
    image {
      localFile {
        childImageSharp {
          gatsbyImageData(layout: FULL_WIDTH)
        }
      }
    }
    author {
      id
      username
    }
  }
}
`